.popular{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    height: 90vh;
    margin-top: 110px;
}
.popular h2{
    font-size: 50px;
    font-weight: 600;
    color: black;
}
.popular hr{
    width: 250px;
    height: 6px;
    border-radius: 10px;
    background: green;
}

.popular-item{
    display: flex;
    margin-top: 50px;
    gap: 30px;
}