.offers{
    height: 70vh;
    width: 70%;
    margin: auto;
    display: flex;
    margin-bottom: 70px;
    margin-top: 70px;
    padding: 50px 140px;
    background: linear-gradient(180deg , #fde1ff , #e1ffea22 60%);
}
.offers-left{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.offers-left h1{
    font-size: 70px;
    font-weight: 600;
    color: black;
}
.offers-left p{
    font-size: 22px;
    font-weight: 600;
    color: black;
}
.offers-left button{
    height: 55px;
    width: 190px;
    padding: 5px;
    background: green;
    color: white;
    font-size: 22px;
    font-weight: 500;
    border-radius: 70px;
    margin-top: 20px;
    cursor: pointer;
    outline: none;
    border: none;
}
.offers-left button:hover{
    background: white;
    color: green;
    transform: scale(1.05);
    transition: 0.6s;
    border: 2px solid green;
    font-weight: 500;
}
.offers-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: flex-end;
}