.hero{
    display: flex;
    height: 100vh;
    background: linear-gradient(180deg , #fde1ff , #e1ffea22 60%);
}

.hero-left{
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;

}
.hero-left span{
    color: green;
}
.hero-left h2{
    font-size: 26px;
    font-weight: 600;
    color: black;
}
.hero-left p{
    font-size: 85px;
    font-weight: 700;
    color: black;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;
}
.hero-hand-icon img{
    width: 100px;
}
.hero-latest-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 55px;
    width: 265px;
    gap: 18px;
    background: green;
    color: white;
    margin-top: 30px;
    border-radius: 75px;
    font-size: 20px;
    font-weight: 550;
}
.hero-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
}