.news-letter{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 80%;
    height: 60vh;
    margin: auto;
    margin-top: 50px;
    gap: 20px;
    padding: 0px 20px;
    background: linear-gradient(180deg , #fde1ff , #e1ffea22 60%);
}
.news-letter h1{
    font-size: 50px;
    font-weight: 600;
    color: black;
}
.news-letter p{
    font-size: 25px;
    font-weight: 500;
    color: green;
}
.news-letter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    width: 650px;
    border: 2px solid green;
    border-radius: 30px;
}
.news-letter input{
    border: none;
    outline: none;
    font-size: 28px;
    color: black;
    padding: 10px;
    margin-left: 30px;
    width: 500px;
}
.news-letter div button{
    width: 210px;
    height: 60px;
    font-size: 24px;
    font-weight: 500;
    background: green;
    color: white;
    cursor: pointer;
    border-radius: 80px;
}