.loginsignup{
    width: 100%;
    height: 90vh;
    padding-top: 100px;
    background: linear-gradient(180deg , #fde1ff , #e1ffea22 60%);
}

.loginsignup-container{
    height: 580px;
    width: 500px;
    margin: auto;
    padding: 30px 60px;
    background: white;
}
.loginsignup-container h1{
    margin: 20px 0px;
}
.loginsignup-fields{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    margin-top: 30px;
}
.loginsignup-fields input{
    width: 100%;
    height: 50px;
    padding-left: 20px;
    border: 2px solid black;
    font-size: 18px;
    font-weight: 550;
    outline: none;
    border-radius: 10px;
}
.loginsignup-container button{
    height: 60px;
    width: 490px;
    margin-top: 30px;
    background: green;
    font-size: 28px;
    font-weight: 500;
    color: white;
    cursor: pointer;
    border-radius: 10px;
}
.loginsignup-login{
    font-size: 18px;
    color: black;
    margin-top: 30px;
}
.loginsignup-login span{
    color: green;
    cursor: pointer;
}
.loginsignup-agree{
    display: flex;
    align-items: center;
    justify-content: start;
    gap: 15px;
    margin-top: 20px;
}