.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 50px;
    margin-top: 20px;
}
.footer-logo{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
}
.footer-logo h1{
    font-size: 35px;
    font-weight: 600;
    color: green;
}
.footer-links{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
}
.footer-links li{
    list-style-type: none;
    font-size: 22px;
    font-weight: 500;
    color: black;
    cursor: pointer;
}
.footer-social-links{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
    cursor: pointer;
}
.footer-social-icons{
    border: 3px solid green;
    padding: 8px;
    border-radius: 10px;
}
.footer-social-icons:hover{
    transform: scale(1.05);
    transition: 0.5s;
}
.footer-copyright{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    gap: 30px;
    font-size: 22px;
    margin-bottom: 30px;
}
.footer-copyright hr{
    width: 80%;
    height: 2px;
    background-color: green;
    border-radius: 10px;
}