.item{
    width: 350px;
}
item p{
    margin: 6px 0px;
}
.item-price{
    display: flex;
    gap: 20px;
}
.item-price-new{
    font-size: 18px;
    font-weight: 600;
    color: green;
}
.item-price-old{
    font-size: 18px;
    font-weight: 500;
    color: black;
    text-decoration: line-through;
}
.item:hover{
    transform: scale(1.05);
    transition: 0.4s;
}

