.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    font-size: 38px;
    font-weight: 600;
    color: green;
}
.nav-menu{
    display: flex;
    align-items: cente;
    gap: 50px;
    list-style-type: none;
    font-size: 20px;
    font-weight: 600;
    color: black;
}
.nav-menu li{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    cursor: pointer;
    gap: 3px;
}
.nav-menu hr{
    width: 80%;
    height: 3px;
    border: none;
    background: green;
    border-radius: 10px;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 50px;
}
.nav-login-cart button{
    height: 58px;
    width: 147px;
    font-size: 25px;
    color: white;
    background-color: green;
    border: none;
    outline: none;
    border: 2px solid grey;
    border-radius: 45px;
    cursor: pointer;
}
.nav-login-cart button:active{
    color: green;
    background: white;
}
.nav-cart-count{
    margin-top: -35px;
    margin-left: -55px;
    background: green;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 24px;
    width: 24px;
    border-radius: 11px;
    color: white;
}