.shopcategory-banner{
    display: block;
    width: 82%;
    margin: 30px auto;
}
.shopcategory-indexSort{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0px 150px;
}
.shopcategory-sort{
    border: 1px solid black;
    padding: 10px 20px;
    border-radius: 40px;
}
.shopcategory-products{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 20px 150px;
    row-gap: 60px;
    gap: 20px;
}
.shopcategory-loadmore{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    width: 200px;
    margin: 120px auto;
    background-color: green;
    color: white;
    font-size: 22px;
    font-weight: 500;
    border-radius: 50px;
    cursor: pointer;
}
.shopcategory-loadmore:hover{
    background: white;
    color: green;
    transform: scaleY(1.05);
    transition: 0.6s;
    border: 1px solid green;
}
