.new-collections{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 100px;
}
.new-collections h1{
    font-size: 50px;
    font-weight: 600;
    color: black;
}
.new-collections hr{
    width: 250px;
    height: 6px;
    border-radius: 10px;
    background: green;
}

.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 70px;
    gap: 30px;
}